:root {
    --color-highlight-fire: rgba(255, 236, 168, 1);
    --color-primary-fire: rgba(255, 138, 48, 1);
    --color-secondary-fire: rgba(240, 96, 29, 1);
    --color-highlight-ice: rgba(168, 236, 255, 1);
    --color-primary-ice: rgba(48, 138, 255, 1);
    --color-secondary-ice: rgba(29, 96, 240, 1);
    --background-gradient-start: #010101;
    --background-gradient-end: #0a0000;
    --background: 0 0% 100%;
    --foreground: 240 10% 3.9%;
    --card: 0 0% 100%;
    --card-foreground: 240 10% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 240 10% 3.9%;
    --primary: 240 5.9% 10%;
    --primary-foreground: 0 0% 98%;
    --secondary: 240 4.8% 95.9%;
    --secondary-foreground: 240 5.9% 10%;
    --muted: 240 4.8% 95.9%;
    --muted-foreground: 240 3.8% 46.1%;
    --accent: 240 4.8% 95.9%;
    --accent-foreground: 240 5.9% 10%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 5.9% 90%;
    --input: 240 5.9% 90%;
    --ring: 240 5.9% 10%;
    --radius: 0.5rem;
}

.home-page {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}
/* HomePage.css */
.genre-selector {
    margin: 20px;
    text-align: center;
}

.genre-selector select {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}


.home_content {
    width: 100%;
    box-sizing: border-box;
    flex-grow: 1;

}
/* StarryBackground.css */
.starry-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: black;
    overflow: hidden;

}
